body {
  ---zoom: 100;
  ---board-scale: calc((var(---zoom) / 100) * .65 + .35);
}

.board-wrapper {
  min-width: calc(var(---board-scale) * 40rem);
  width: calc(var(---board-scale) * 40rem);
  height: calc(var(---board-scale) * 40rem);
}

.movelist {
  height: calc(var(---board-scale) * 40rem - 51px);
}

.cg-wrap {
  width: 100%;
  height: 100%;
  display: block;
  position: relative;
}

cg-helper {
  width: 12.5%;
  padding-bottom: 12.5%;
  display: table;
  position: absolute;
  bottom: 0;
}

cg-container {
  display: block;
  position: absolute;
  bottom: 0;
}

cg-board {
  -webkit-user-select: none;
  user-select: none;
  cursor: pointer;
  background-size: cover;
  width: 100%;
  height: 100%;
  line-height: 0;
  position: absolute;
  top: 0;
  left: 0;
}

cg-board square {
  pointer-events: none;
  width: 12.5%;
  height: 12.5%;
  position: absolute;
  top: 0;
  left: 0;
}

cg-board square.move-dest {
  pointer-events: auto;
  background: radial-gradient(#14551e80 22%, #208530 0, #0000004d 0, #0000 0);
}

cg-board square.premove-dest {
  background: radial-gradient(#141e5580 22%, #203085 0, #0000004d 0, #0000 0);
}

cg-board square.oc.move-dest {
  background: radial-gradient(#0000 0% 80%, #1455004d 80%);
}

cg-board square.oc.premove-dest {
  background: radial-gradient(#0000 0% 80%, #141e5533 80%);
}

cg-board square.move-dest:hover {
  background: #14551e4d;
}

cg-board square.premove-dest:hover {
  background: #141e5533;
}

cg-board square.last-move {
  will-change: transform;
  background-color: #9bc70069;
}

cg-board square.selected {
  background-color: #14551e80;
}

cg-board square.check {
  background: radial-gradient(red 0%, #e70000 25%, #a9000000 89%, #9e000000 100%);
}

cg-board square.current-premove {
  background-color: #141e5580;
}

.cg-wrap piece {
  z-index: 2;
  will-change: transform;
  pointer-events: none;
  background-size: cover;
  width: 12.5%;
  height: 12.5%;
  position: absolute;
  top: 0;
  left: 0;
}

cg-board piece.dragging {
  cursor: move;
  z-index: 10;
}

cg-board piece.anim {
  z-index: 8;
}

cg-board piece.fading {
  z-index: 1;
  opacity: .5;
}

.cg-wrap square.move-dest:hover {
  background-color: #14551e4d;
}

.cg-wrap piece.ghost {
  opacity: .3;
}

.cg-wrap .cg-shapes, .cg-wrap .cg-custom-svgs {
  pointer-events: none;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
}

.cg-wrap .cg-shapes {
  opacity: .6;
  z-index: 2;
}

.cg-wrap .cg-custom-svgs {
  z-index: 9;
}

.cg-wrap coords {
  pointer-events: none;
  opacity: .7;
  color: #000;
  font-size: 11px;
  font-weight: bold;
  display: flex;
  position: absolute;
}

.cg-wrap coords.ranks {
  flex-flow: column-reverse;
  width: 12px;
  height: 100%;
  top: 0;
  right: -2px;
}

.cg-wrap coords.ranks.black {
  flex-flow: column;
}

.cg-wrap coords.files {
  text-transform: lowercase;
  flex-flow: row;
  width: 100%;
  height: 16px;
  bottom: 0;
  left: 4px;
}

.cg-wrap coords.files.black {
  flex-flow: row-reverse;
}

.cg-wrap coords coord {
  flex: auto;
}

cg-resize {
  cursor: nwse-resize;
  z-index: 10;
  width: 22px;
  height: 22px;
  display: block;
  position: absolute;
  bottom: -9px;
  right: -9px;
}

cg-resize:before, cg-resize:after {
  content: "";
  background: #000;
  width: 12px;
  height: 1px;
  position: absolute;
  left: 0;
}

cg-resize:before {
  width: 5px;
  transform: translate(7px, 8px)rotate(-45deg);
}

cg-resize:after {
  transform: translate(1px, 6px)rotate(-45deg);
}

cg-resize:hover {
  background: #d64f0080;
  border-radius: 50%;
}

.resizing cg-resize {
  background: #62992480;
  border-radius: 50%;
}

#promotion-choice {
  z-index: 205;
  width: var(---cg-width, 100%);
  height: var(---cg-height, 100%);
  background-color: #262421b3;
  position: absolute;
  bottom: 0;

  & square {
    cursor: pointer;
    pointer-events: all;
    background-color: #b0b0b0;
    border-radius: 50%;
    width: 12.5%;
    height: 12.5%;
    transition: all .15s;
    position: absolute;
    top: 0;
    left: 0;
    box-shadow: inset 0 0 25px 3px gray;
  }

  & square:hover {
    border-radius: 0%;
    box-shadow: inset 0 0 48px 8px #d64f00;
  }

  & piece {
    pointer-events: none;
    opacity: 1;
    width: 100%;
    height: 100%;
    transition: all .15s;
    display: inline-block;
    transform: scale(.8);
  }

  & square:hover piece {
    transform: none;
  }
}
